<script>
var graceJS = require('@/Grace.JS/grace.js');
export default {
	onLaunch: function() {
		console.log('App Launch');
	},
	onShow: function() {
		//判断当前版本
		// #ifdef APP-PLUS
		// let system = graceJS.system();
		// console.log(system);
		var plantForm = '';
		uni.getSystemInfo({
			success: (res) => {
				// console.log(res.platform);
				if (res.platform == "android") {
					plantForm = 'android'
				} else if (res.platform == "ios") {
					plantForm = 'ios'
				} else {
					plantForm = 'miniprogram'
				}
			}
		})
		
		var me = this;
		var serverUrl = me.getServerUrl();
		uni.request({
			url: serverUrl + '/AppVersion/Get?appCode=com.pandianapp.warehouse&platform=' + plantForm,
			method: 'GET',
			header: {
				appKey: me.appKey,
				appSecret: me.appSecret
			},
			success: res => {
				if (res.data.Status) {
					console.log(serverUrl);
					console.log(res.data);
					// console.log(plus.runtime.versionCode);
					plus.runtime.getProperty(plus.runtime.appid, function(widgetInfo) {
						// console.log(widgetInfo);
						if (res.data.Data.VersionCode > widgetInfo.versionCode) {
							uni.showModal({
								title: '更新提示',
								content: '发现新版本:' + res.data.Data.VersionName + '，为保障新功能正常运行，请立即升级！',
								showCancel: false,
								success: function(e) {
									if (e.confirm) {
										uni.reLaunch({
											url: '../../pages/version/version?url=' + res.data.Data.Url
										});
									} else if (e.cancel) {
										uni.showToast({
											title: '为保障新功能正常运行，请立即升级！',
											mask: true,
											duration: 2000,
											icon: 'none'
										});
									}
								}
							});
						}
					});
				} else {
					// uni.showToast({
					// 	title: res.data.StatusMessage,
					// 	duration: 2000,
					// 	icon: 'none'
					// });
				}
			},
			fail: function() {
				uni.showToast({
					title: '网络异常请重试',
					duration: 2000,
					icon: 'none'
				});
			},
			complete: function() {
				setTimeout(function() {
					uni.hideLoading();
				}, 2000);
			}
		});
		// #endif
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide');
	}
};
</script>

